import { langs } from "../store/reducer/LangReducer";

function setUserLocale(locale) {
  localStorage.setItem('userLocale', locale);
}

export class LangHeader {  
  menuMobile = document.querySelector('.menu-mobile');
  menuTop = document.querySelector('.menu-top');
  lang = '';
  constructor(lang) {
    this.lang = lang;
    this.changeLang(lang)
  }  

  changeLang(lang) {
    switch (lang) {
      case langs.RUS:
        this.rus()
        break;
      
      case langs.USA:
        this.eng()
        break;
      
      case langs.UAE:
        this.aze()
        break;    
      
      case langs.IND:
        this.ind()
         break;    
        
      case langs.CHN:
        this.chn()
        break;    
        
      default:
        break;
    }
  }

  rus() {
    this.menuMobile.children[0].children[0].textContent = "";
    this.menuMobile.children[1].children[0].textContent = " ";
    this.menuMobile.children[2].children[0].textContent = " ";
    this.menuMobile.children[3].children[0].textContent = " ";
    this.menuMobile.children[4].children[0].textContent = "FAQ";
  
    this.menuTop.children[0].children[0].textContent = " ";
    this.menuTop.children[1].children[0].textContent = "";
    this.menuTop.children[2].children[0].textContent = " ";
    this.menuTop.children[3].children[0].textContent = "FAQ";
  }

  eng() {
    this.menuMobile.children[0].children[0].textContent = "Main";
    this.menuMobile.children[1].children[0].textContent = "About";
    this.menuMobile.children[2].children[0].textContent = "Road map";
    this.menuMobile.children[3].children[0].textContent = "How to buy";
    this.menuMobile.children[4].children[0].textContent = "FAQ";
  
    this.menuTop.children[0].children[0].textContent = "About";
    this.menuTop.children[1].children[0].textContent = "Road map";
    this.menuTop.children[2].children[0].textContent = "How to buy";
    this.menuTop.children[3].children[0].textContent = "FAQ";
  }

  aze() {
    this.menuMobile.children[0].children[0].textContent = " ";
    this.menuMobile.children[1].children[0].textContent = " ";
    this.menuMobile.children[2].children[0].textContent = " ";
    this.menuMobile.children[3].children[0].textContent = " ";
    this.menuMobile.children[4].children[0].textContent = "";
  
    this.menuTop.children[0].children[0].textContent = " ";
    this.menuTop.children[1].children[0].textContent = " ";
    this.menuTop.children[2].children[0].textContent = " ";
    this.menuTop.children[3].children[0].textContent = "";
  }

  chn() {  
    this.menuMobile.children[0].children[0].textContent = "";
    this.menuMobile.children[1].children[0].textContent = "";
    this.menuMobile.children[2].children[0].textContent = "";
    this.menuMobile.children[3].children[0].textContent = "";
    this.menuMobile.children[4].children[0].textContent = "";
  
    this.menuTop.children[0].children[0].textContent = "";
    this.menuTop.children[1].children[0].textContent = "";
    this.menuTop.children[2].children[0].textContent = "";
    this.menuTop.children[3].children[0].textContent = "";
  }

  ind() {
    this.menuMobile.children[0].children[0].textContent = "";
    this.menuMobile.children[1].children[0].textContent = "  ";
    this.menuMobile.children[2].children[0].textContent = " ";
    this.menuMobile.children[3].children[0].textContent = " ";
    this.menuMobile.children[4].children[0].textContent = " ";
  
    this.menuTop.children[0].children[0].textContent = "  ";
    this.menuTop.children[1].children[0].textContent = " ";
    this.menuTop.children[2].children[0].textContent = " ";
    this.menuTop.children[3].children[0].textContent = " ";
  }
}








